import React, { useState, useEffect } from "react";
import openSocket from "socket.io-client";

import Giphy from "./components/Giphy";
import PrettoSlider from "./components/PrettoSlider";
import "./App.css";

const socket = openSocket("https://6172e9f5.ngrok.io");

const App = () => {
  // state
  const [nowPlaying, setNowPlaying] = useState({});
  const [available, setAvailable] = useState(false);
  const [count, setCount] = useState(0);
  const [parameters, setParameters] = useState([
    { name: "energy", value: 50, avg: 50, active: false }
  ]);
  const [votes, setVotes] = useState([50]);
  const [touch, setTouch] = useState(false);

  // init
  socket.on("now playing", nowPlaying => {
    setNowPlaying(nowPlaying);
  });

  // when peopleValue updates, send data to server
  useEffect(() => {
    console.log(parameters);
    socket.emit("new value", parameters);
  }, [votes]);

  const changeParameter = (name, event, value) => {
    const newParameters = parameters.map(parameter => {
      if (parameter.name !== name) {
        return parameter;
      }
      return {
        ...parameter,
        value,
        active: true
      };
    });

    setParameters(newParameters);
  };

  const touchEnd = (name, event, value) => {
    const threshold = 8;
    let avg = votes.reduce((a, b) => a + b, 0) / votes.length || 0;
    if (votes.length > threshold) {
      avg =
        votes
          .slice(votes.length - threshold, votes.length)
          .reduce((a, b) => a + b, 0) / threshold || 0;
    }
    const newParameters = parameters.map(parameter => {
      if (parameter.name !== name) {
        return parameter;
      }

      return {
        ...parameter,
        value: Math.round(avg),
        avg: Math.round(avg),
        active: false
      };
    });

    setParameters(newParameters);
    setVotes([...votes, value]);
  };

  /*
  const addParameter = (name, event) => {
    if (name in parameters) {
      return parameters;
    }

    setParameters([...parameters, { name, value: 0 }]);
  };

  const removeParameter = (name, event) => {
    const newParameters = parameters.filter(
      parameter => name !== parameter.name
    );
    setParameters(newParameters);
  };

  useEffect(() => {
    if (count > 20) setAvailable(true);
  });

  const handlePlay = () => {
    setCount(0);
    setAvailable(false);
    socket.emit("next", true);
  };
  */

  return (
    <div className="wrapper" onDoubleClick={e => e.preventDefault()}>
      <div className={touch ? "App touch" : "App"}>
        <h1>Control the mood!</h1>
        <h3>Be a part of controlling the ambience by adjusting the music</h3>
        <Giphy q={parameters[0].avg} />
        <div className="controls">
          {parameters.map(parameter => (
            <Parameter
              key={parameter.name}
              data={parameter}
              handleChange={changeParameter.bind(null, parameter.name)}
              touchEnd={touchEnd.bind(null, parameter.name)}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

const Parameter = ({ data, handleChange, touchEnd }) => {
  const { name, value, active } = data;
  return (
    <PrettoSlider
      className={active ? "slider active" : "slider"}
      onChangeCommitted={touchEnd}
      valueLabelDisplay="auto"
      aria-label={`slider for ${name}`}
      value={value}
      onChange={handleChange}
      min={1}
      max={100}
    />
  );
};

const NowPlaying = ({ data }) => {
  const { album, artists, name } = data;
  return (
    <div>
      <div>
        <img src={album.images[2].url} alt="album cover" />
      </div>
      <div>{name}</div>
      <div>{artists[0].name}</div>
    </div>
  );
};

export default App;
