import React, { useState, useEffect } from "react";

const Giphy = props => {
  const gifs = q => {
    switch (true) {
      case q > 0 && q < 10:
        return "standing still";
      case q > 9 && q < 20:
        return "listen to music";
      case q > 19 && q < 30:
        return "hiphop";
      case q > 29 && q < 40:
        return "dance";
      case q > 39 && q < 50:
        return "moderate dance";
      case q > 49 && q < 60:
        return "energetic dance";
      case q > 59 && q < 70:
        return "hyper dance";
      case q > 69 && q < 80:
        return "hyper energetic dance";
      case q > 79 && q < 90:
        return "hyper energetic dance";
      case q > 89 && q < 100:
        return "freak out";

      default:
        return "listen to music";
    }
  };

  const getRandomInt = (min, max) => {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  };

  const url = "https://api.giphy.com/v1/gifs/search?api_key=";
  const key = "lUxuo0G2Dg5qHDcas6AXWCY2RY8Y0td7";
  const q = `&q=${encodeURI(gifs(props.q))}&limit=1&offset=0&rating=G&lang=en`;

  const [gif, setGif] = useState({});
  const [value, setValue] = useState(50);

  useEffect(() => {
    setValue(props.q);
    fetch(url + key + q)
      .then(response => {
        return response.json();
      })
      .then(data => {
        setGif(data);
      });
  }, [props.q]);

  if (gif.data && gif.data.length > 0) {
    let randomInt = getRandomInt(0, gif.data.length - 1);
    return (
      <div className="gifContainer">
        <img
          src={gif.data[randomInt].images.downsized_medium.url}
          width={gif.data[randomInt].images.downsized_medium.width}
        />
      </div>
    );
  }
  return <div></div>;
};

export default Giphy;
